<template>
  <div class="container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>订单</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="tools">
      <el-select v-model="statusValue" id="statusSelect" class="selected" @change="statusChange">
        <el-option
          v-for="item in statusOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <span class="searchAdvanced" @click="showSearchWrap = true">高级搜索</span><i class="el-icon-search"></i>
    </div>
    <div class="searchWrap" v-show="showSearchWrap">
      <transition name="dropDown">
        <div class="searchForm" v-if="showSearchWrap">
          <el-form ref="form" :inline="true" :model="form" label-width="80px">
            <el-row>
              <el-col :span="6">
                <el-form-item size="small" label="订单号">
                  <el-input size="small" type="textarea" v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item size="small" label="物流单号">
                  <el-input size="small" type="textarea" v-model="form.track_number"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item size="small" label="收件人">
                  <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item size="small" label="手机">
                  <el-input v-model="form.mobile" placeholder=""></el-input>
                </el-form-item>
                <el-form-item size="small" label="邮箱">
                  <el-input v-model="form.emial" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item size="small" label="站点ID">
                  <el-input v-model="form.website" placeholder=""></el-input>
                </el-form-item>
                <el-form-item size="small" label="标题">
                  <el-input v-model="form.product" placeholder=""></el-input>
                </el-form-item>
                <el-form-item size="small" label="SPU">
                  <el-input v-model="form.spu" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item size="small" label="地区">
                  <el-select v-model="form.country">
                    <el-option label="香港" value="HK"></el-option>
                    <el-option label="台湾" value="TW"></el-option>
                    <el-option label="泰国" value="TH"></el-option>
                    <el-option label="马来" value="MY"></el-option>
                    <el-option label="新加坡" value="SG"></el-option>
                    <el-option label="阿联酋" value="UAE"></el-option>
                    <el-option label="印尼" value="ID"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item size="small" label="货代">
                  <el-select v-model="form.track">
                    <el-option label="DHL" value="DHL"></el-option>
                    <el-option label="商壹" value="SY"></el-option>
                    <el-option label="顺丰" value="SF"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item size="small" label="订单状态">
                  <el-checkbox-group v-model="form.type">
                    <el-checkbox label="待确认" name="type"></el-checkbox>
                    <el-checkbox label="已确认" name="type"></el-checkbox>
                    <el-checkbox label="待发货" name="type"></el-checkbox>
                    <el-checkbox label="已发货" name="type"></el-checkbox>
                  </el-checkbox-group>
                </el-form-item>
              </el-col>

              <el-col :span="24">
                <el-form-item size="small" label="下单时间">
                  <el-col :span="11">
                    <el-date-picker type="datetime" placeholder="选择日期时间" v-model="form.date1"
                                    style="width: 100%;"></el-date-picker>
                  </el-col>
                  <el-col class="line" :span="2">-</el-col>
                  <el-col :span="11">
                    <el-date-picker type="datetime" placeholder="选择日期时间" v-model="form.date2"
                                    style="width: 100%;"></el-date-picker>
                  </el-col>
                </el-form-item>
                <el-form-item size="small" label="发货时间">
                  <el-col :span="11">
                    <el-date-picker type="datetime" placeholder="选择日期时间" v-model="form.date3"
                                    style="width: 100%;"></el-date-picker>
                  </el-col>
                  <el-col class="line" :span="2">-</el-col>
                  <el-col :span="11">
                    <el-date-picker type="datetime" placeholder="选择日期时间" v-model="form.date4"
                                    style="width: 100%;"></el-date-picker>
                  </el-col>
                </el-form-item>
              </el-col>
            </el-row>

            <el-form-item>
              <el-button @click="resetForm('form')">重置</el-button>
              <el-button @click="showSearchWrap = false">取消</el-button>
              <el-button type="primary" @click="onSubmit">搜索</el-button>
            </el-form-item>
          </el-form>
        </div>
      </transition>
    </div>

    <div class="tableContainer">
      <el-table
        height="650"
        border
        :data="tableData"
        style="width: 100%">
        <el-table-column prop="id" label="订单号"></el-table-column>
        <el-table-column prop="total" label="金额"></el-table-column>
        <el-table-column prop="website" label="站点"></el-table-column>
        <el-table-column prop="country" label="地区"></el-table-column>
        <el-table-column label="收件人" prop="name"></el-table-column>
        <el-table-column label="电话" prop="phone"></el-table-column>
        <el-table-column label="IP" prop="ip"></el-table-column>
        <el-table-column label="客户备注" prop="comment"></el-table-column>
        <el-table-column label="下单时间" prop="create_date"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleDetail(scope.$index, scope.row)">详情
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[100, 200, 300, 400]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalSize">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    data() {
      return {
        totalSize: 0,//默认数据总数
        pageSize: 10,//每页的数据条数
        currentPage: 1,//默认开始页面
        showSearchWrap: false,
        statusValue: '-1',
        statusOptions: [
          {
            value: '-1',
            label: '全部订单'
          },
          {
            value: '0',
            label: '新增'
          }
        ],
        tableData: [],
        form: {
          id: '',
          track_number: '',
          name: '',
          mobile: '',
          website: '',
          product: '',
          spu: '',
          country: '',
          track: '',
          email: '',
          type: [],
          date1: '',
          date2: '',
          date3: '',
          date4: '',
        }
      }
    },
    methods: {
      statusChange() {

      },
      handleSizeChange() {

      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
      },
      handleDetail(index, row) {
        this.$router.push('/order/orderDetail?id=' + row.id);
      },
      onSubmit() {
        var thats = this;
        const instance = axios.create({
          timeout: 5000,
        });
        instance.get('http://192.168.10.141/v1/order/searches',
          {
            params: {
              id: this.form.id,
              track_number: this.form.track_number,
              name: this.form.name,
              mobile: this.form.mobile,
              email: this.form.email,
              product: this.form.product,
              spu: this.form.spu,
              country: this.form.country,
              track: this.form.track,
              type: this.form.type,
              date1: this.form.date1,
              date2: this.form.date2,
            }
          }).then(function (res) {
          thats.tableData = [];
          console.log(res);
          if (res.data.code == 200) {
            var lens = res.data.data.length;
            for (var i = 0; i < lens; i++) {
              thats.tableData.push({
                id: res.data.data[i].id,
                total: res.data.data[i].total_price,
                website: res.data.data[i].website,
                country: res.data.data[i].country,
                name: res.data.data[i].user_name,
                phone: res.data.data[i].phone,
                ip: res.data.data[i].user_ip,
                comment: res.data.data[i].comment,
                create_date: res.data.data[i].create_time,
              })

            }
            thats.total = lens;
          }

        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      getData() {

      }
    },
    created() {
      let that = this;
      axios({
        method: "GET",
        url: 'http://192.168.10.141/v1/orders',
      }).then(function (re) {
        console.log(re.data.data);
        var len = re.data.data.list.length;
        for (var i = 0; i < len; i++) {
          that.tableData.push({
            id: re.data.data.list[i].id,
            total: re.data.data.list[i].total_price,
            website: re.data.data.list[i].website,
            country: re.data.data.list[i].country,
            name: re.data.data.list[i].user_name,
            phone: re.data.data.list[i].phone,
            ip: re.data.data.list[i].user_ip,
            comment: re.data.data.list[i].comment,
            create_date: re.data.data.list[i].create_time,
          })

        }
        that.total = len;
      });

    }
  }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .tools
      margin 20px 0
      .selected
        width: 150px
        margin-right: 20px
      .searchAdvanced
        cursor: pointer
        margin-right: 3px
    .searchWrap
      position fixed
      z-index 11
      left 0
      top 0
      right 0
      bottom 0
      background-color: rgba(0,0,0,0.2)
      .searchForm
        position absolute
        padding: 30px 20px
        left 210px
        top 154px
        right 30px
        background-color: #fff
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 120px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



